  }

  .timeline {
    width: 100%;
    padding-bottom: 0px;
  }

  .timeline:before {
    left: 26px;
  }

  .education {
    margin-bottom: 0px;
    padding-bottom: 0px;
  }

  .timeline ul li:nth-child(odd),
  .timeline ul li:nth-child(even) {
    margin-top: 50px;
    width: 100%;
    text-align: left;
    padding-left: 50px;
    padding-right: 50px;
  }

  .timeline ul li:nth-child(odd):before,
  .timeline ul li:nth-child(even):before {
    top: -17px;
    left: 23px;
  }

  .timeline ul li:nth-child(odd) .time,
  .timeline ul li:nth-child(even) .time {
    top: -30px;
    left: 50px;
    right: inherit;
  }
}

